import React from "react";

export const Home = () => {
  return (
    <div className="container">
      <div className="row">
        <blockquote className="red-text">
          We are open with all classes in-person. New and returning students, as
          well as visitors, are welcome. Drop-ins okay for Mysore classes only.
          Sign-up required for weekend led classes. Zoom option for weekend
          classes. Find the links on the Schedule page.
        </blockquote>
      </div>

      <div className="row">
        <div className="card">
          <div className="card-image">
            <img
              src="photo_index.jpg"
              style={{ filter: "brightness(70%)" }}
            ></img>
            <span className="card-title purple-text text-lighten-5">
              <b style={{ fontSize: "25px" }}>Purple Yoga Hawaii</b>
            </span>
          </div>
          <div className="card-content">
            <p className="purple-text">
              We are dedicated to a progressive way of teaching, with a
              long-term view of improving our students' physical, mental and
              spiritual well-being. We are mindful of each person's needs and
              abilities, and promote a supportive, friendly environment for
              inspired learning and sound practice.
            </p>
          </div>
        </div>
      </div>

      <div className="row">
        <div className="col s6">
          <p className="grey-text" style={{ maxWidth: "300px" }}>
            Aloha from the owners of Purple Cathy Louise Broda and Glen Butcher{" "}
            <br></br>
            <span className="black-text">
              (808) 944-8585 <b>info@purpleyoga.com</b>
            </span>
          </p>
        </div>
        <div className="col s6">
          <p className="right grey-text" style={{ maxWidth: "200px" }}>
            "I think it pisses God off if you walk by the color purple in a
            field somewhere and don't notice it." <br></br>
            <span className="black-text">
              <em>Alice Walker, The Color Purple</em>
            </span>
          </p>
        </div>
      </div>
    </div>
  );
};
