import React from "react";
import "./workshops.css";

export const Workshops = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col s12 m12 l6">
          <Content1 />
          <Content2 />
          <Content3 />
        </div>
        <div className="col s12 m12 l6">
          <Content4 />
        </div>
      </div>
    </div>
  );
};

const Content4 = () => {
  return (
    <div className="card">
      <div className="card-image">
        <img src="photo_ponytail.jpg" />
      </div>
      <div className="card-content">
        <p>
          Cathy Louise and Glen have 50 years combined teaching experience. All
          levels of Ashtanga practitioners are welcome to this training,
          including beginners. Everyone has a story of practice, a narrative
          interwoven with experience of love, loss, work and play. We
          acknowledge your story and want to help you write the next chapter.
        </p>
        <br />
        <p>
          We encourage attendees to join our regular early morning Mysore
          practice sessions on Friday, Sunday and Monday from 6:00–8:30am (to be
          paid separately). A led class will be taught within the training on
          Saturday.
        </p>
        <br />
        <p>
          All classes are at Purple Yoga Hawaii. Enquiries info@purpleyoga.com
          or text/phone (808) 944-8585.
        </p>
        <br />
        <blockquote
          className="grey-text c4-div"
          style={{ fontWeight: "bold", fontSize: "12px" }}
        >
          <p>Space is limited, please register early to secure your place.</p>
          <br />
          <p>
            Early bird price $525 until April 10. From April 11 the price is
            $600.
          </p>
          <br />
          <p>
            You may cancel up until May 13 and your payment will be refunded
            less a fee of 10%. After this date your payment is non-refundable.
          </p>
          <br />
          <p>
            Checks are welcome as payment. Please contact us for instructions.
          </p>
          <br />
          <p>
            Purple Yoga Hawaii is not responsible for your travel, accommodation
            or other expenses.
          </p>
          <br />
        </blockquote>
      </div>
    </div>
  );
};

const Content3 = () => {
  return (
    <ul className="collection with-header">
      <li className="collection-header">
        <h5>We’ll also explore the:</h5>
      </li>
      <li className="collection-item">
        tools of practice: drishti, bandhas, breath, asana and vinyasa
      </li>
      <li className="collection-item">
        evolution of Ashtanga Yoga and its influence on yoga today
      </li>
      <li className="collection-item">
        rules and restrictions surrounding the practice of Ashtanga
      </li>
      <li className="collection-item">
        recommended resources for study: books, videos, websites
      </li>
      <li className="collection-item">joy of practice.</li>
    </ul>
  );
};
const Content2 = () => {
  return (
    <ul className="collection with-header">
      <li className="collection-header">
        <h5>We’ll teach you how to:</h5>
      </li>
      <li className="collection-item">
        practice for different stages of life for women and men
      </li>
      <li className="collection-item">
        modify practice for pain, injuries and physical disabilities
      </li>
      <li className="collection-item">
        see the effects of emotional and mental health on practice
      </li>
      <li className="collection-item">
        interpret both corrective and enhancing hands-on adjustments
      </li>
      <li className="collection-item">
        cultivate self-evaluation so you can be your own best guide.
      </li>
    </ul>
  );
};
const Content1 = () => {
  return (
    <ul className="collection with-header">
      <li className="collection-header">
        <h4 className="purple-text">
          Ashtanga Yoga Training: Mastering Mysore Practice
        </h4>
      </li>
      <li className="collection-item">
        <span className="purple-text">
          May 22–25, 2020 with Cathy Louise Broda and Glen Butcher Fri, Sun, Mon
          9:00–11:30am, 1:00–2:30pm and 3:00–4:30pm Sat 6:30–8:30am,
          9:00–11:30am, 1:00–2:30pm, 3:00–4:30pm
        </span>
        <br />
        <span className="red-text">
          Event cancelled. New dates to be decided later.
        </span>
      </li>
      <li className="collection-item grey-text">
        The Ashtanga adage “Do your practice and all is coming” motivates us to
        get on the mat and work through our mind-body stuff. It’s true regular
        practice reaps great rewards, but blind faith in the method and your
        teacher does not. You the practitioner are responsible for internalizing
        instruction to feed your inner teacher. Inquiring into and applying the
        knowledge given is how you really make the most of Mysore practice.
      </li>
      <li className="collection-item grey-text">
        Ashtanga Yoga is a living tradition. At Purple Yoga Hawaii we teach the
        method as we were taught and incorporate decades of our own experience.
        In our Mysore practice classes, students are encouraged to learn from
        the teacher, fellow students and, most importantly, themselves. Over
        four days of this training we'll show you how to become a more
        observant, independent, and resilient practitioner. The long weekend of
        discussion, lecture, demonstration, hands-on work and asana practice
        will include plenty of time for personal contribution and reflection.
      </li>
    </ul>
  );
};
