import React from "react";

export const ScheduleTable = ({ data }) => {
  return (
    <table className="striped centered">
      <thead>
        <tr>
          <th>Class</th>
          <th>Teacher(s)</th>
          <th>-</th>
        </tr>
      </thead>
      <tbody>
        {data.map((record, index) => (
          <tr key={"scheduletable" + index}>
            <td>
              <span className="link-text">{record.classObject.name}</span>
            </td>
            <td>
              <ul className="collection">
                {record.teachers.map((teacher, index) => (
                  <li
                    key={"teacher" + index}
                    className="collection-item"
                    style={{ background: "rgba(0,0,0,0)" }}
                  >
                    <span className="link-text">{teacher.name}</span>
                  </li>
                ))}
              </ul>
            </td>
            <td>
              <span className="grey-text">{record.schedule}</span>
              {record.message ? (
                <>
                  <br></br>
                  <span style={{ color: "red", fontSize: "11px" }}>
                    {record.message}
                  </span>
                </>
              ) : (
                <></>
              )}
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};
