import "./shop.css";
import React from "react";

export const Shop = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col l4 offset-l2">
          <CardTote />
          <CardTenugui />
        </div>

        <div className="col l4">
          <ul className="collection with-header">
            <li className="collection-header">
              <h5 className="purple-text">To Order</h5>
            </li>
            <li className="collection-item">
              Choose from the purchase options below. Shipping to USA addresses
              only. Hawaii General Excise Tax plus shipping cost will show on
              the order form. For any variation of order other than provided
              below, or to ship overseas, please email your request and we will
              send you a special link.
            </li>
            <li className="collection-item">
              <button
                class="waves-effect waves-light btn"
                style={{ width: "100%" }}
              >
                One Tote
              </button>
              <br />
              <button
                class="waves-effect waves-light btn"
                style={{ width: "100%", marginTop: "10px" }}
              >
                One Tenugui
              </button>
              <br />
              <button
                class="waves-effect waves-light btn"
                style={{ width: "100%", marginTop: "10px" }}
              >
                Onte Tote + One Tenugui
              </button>
              <br />
              <button
                class="waves-effect waves-light btn"
                style={{ width: "100%", marginTop: "10px" }}
              >
                One Rug + One Tenugui
              </button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

const CardTote = () => {
  return (
    <div className="card">
      <div className="card-image">
        <img src="photo_tote.jpg" style={{ height: "350px" }} />
      </div>
      <div className="card-content card-content-shop">
        <span className="card-title" style={{ fontWeight: "bold" }}>
          Canvas Tote Bag $24
        </span>
        <p>
          Handmade and printed by Cathy Louise in Honolulu. Purple on natural
          canvas. Height 13", width 17", depth 3.5", handle 24".
        </p>
      </div>
    </div>
  );
};

const CardTenugui = () => {
  return (
    <div className="card">
      <div className="card-image">
        <img src="photo_tenugui.jpg" style={{ height: "350px" }} />
      </div>
      <div className="card-content card-content-shop">
        <span className="card-title" style={{ fontWeight: "bold" }}>
          Tenugui Towel $18
        </span>
        <p style={{ fontSize: "13px" }}>
          The go-to hand towel for many Ashtanga Yoga practitioners in Hawaii is
          a Japanese tenugui. Made from 100% light cotton, our towel follows
          tradition by having unfinished edges. Our tenugui is hand-printed in
          Honolulu, using cloth sourced from Okinawa. Purple on white, 13" x
          33".
        </p>
      </div>
    </div>
  );
};
