import React, { useEffect } from "react";
import M from "materialize-css";
import "./classespage.css";
import { CardAshtangaMysore } from "./cards/CardAshtangaMysore";
import { CardAshtangaLed } from "./cards/CardAshtangaLed";
import { CardVinyasaFlow } from "./cards/CardVinyasaFlow";
import { CardPregnancy } from "./cards/CardPregnancy";
import { CardMomNBaby } from "./cards/CardMomNBaby";

export const ClassesPage = () => {
  var handleScroll = (id) => {
    document.getElementById(id).scrollIntoView();
  };

  useEffect(() => {
    var elem = document.querySelector(".fixed-action-btn");
    document.addEventListener("DOMContentLoaded", function () {
      var instances = M.FloatingActionButton.init(elem, {});
    });

    window.addEventListener("scroll", (ev) => {
      if (!elem) return;
      if (window.scrollY >= 200) {
        elem.classList.remove("hide");
      } else {
        elem.classList.add("hide");
      }
    });
  });
  return (
    <div className="container">
      <div
        className="row buttons-option hide-on-large-only"
        style={{ paddingTop: "2em" }}
      >
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5 button-first-option"
            onClick={() => handleScroll("ashtanga")}
          >
            Ashtanga Mysore
          </button>
        </div>
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5"
            onClick={() => handleScroll("ashtanga-led")}
          >
            Ashtanga Led
          </button>
        </div>
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5"
            onClick={() => handleScroll("vinyasa")}
          >
            Vinyasa Flow
          </button>
        </div>
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5"
            onClick={() => handleScroll("pregnancy")}
          >
            Pregnancy
          </button>
        </div>
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5"
            onClick={() => handleScroll("momnbaby")}
          >
            Mom &amp; Baby
          </button>
        </div>
      </div>

      <div className="row">
        <div className="col s12 l6 offset-l2">
          <CardAshtangaMysore />
          <CardAshtangaLed />
          <CardVinyasaFlow />
          <CardPregnancy />
          <CardMomNBaby />
        </div>
        <div
          className="col l3 buttons-option hide-on-med-and-down"
          style={{ paddingTop: "1em" }}
        >
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5 button-first-option"
              onClick={() => handleScroll("ashtanga")}
            >
              Ashtanga Mysore
            </button>
          </div>
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5"
              onClick={() => handleScroll("ashtanga-led")}
            >
              Ashtanga Led
            </button>
          </div>
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5"
              onClick={() => handleScroll("vinyasa")}
            >
              Vinyasa Flow
            </button>
          </div>
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5"
              onClick={() => handleScroll("pregnancy")}
            >
              Pregnancy
            </button>
          </div>
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5"
              onClick={() => handleScroll("momnbaby")}
            >
              Mom &amp; Baby
            </button>
          </div>
        </div>
      </div>

      <div className="fixed-action-btn hide" style={{ marginBottom: "27px" }}>
        <button
          className="btn-floating btn-large blue"
          onClick={() => {
            window.scrollTo({
              top: 0,
              behavior: "smooth",
            });
          }}
        >
          <i className="material-icons">arrow_upward</i>
        </button>
      </div>
    </div>
  );
};
