import React, { Fragment, useEffect } from "react";
import M from "materialize-css";
import { Link, NavLink, useHistory } from "react-router-dom";
import "./navbar.css";

export const Navbar = () => {
  let history = useHistory();
  let instance = null;

  useEffect(() => {
    var elem = document.querySelector(".sidenav");
    instance = M.Sidenav.init(elem, { inDuration: 100, outDuration: 100 });
  });

  return (
    <Fragment>
      <nav className="purple darken-4">
        <div className="nav-wrapper">
          <img
            src="logo.gif"
            alt="logo"
            className="brand-logo center"
            style={{ height: "100%" }}
          />
          <a href="#" data-target="mobile-demo" className="sidenav-trigger">
            <i className="material-icons">menu</i>
          </a>
          <ul className="left hide-on-med-and-down">
            <li>
              <NavLink to="/home">Home</NavLink>
            </li>
            <li>
              <NavLink to="/schedule">Schedule</NavLink>
            </li>
            <li>
              <NavLink to="/classes">Classes</NavLink>
            </li>
            <li>
              <NavLink to="/workshops">Workshops</NavLink>
            </li>
            <li>
              <NavLink to="/profiles">Profiles</NavLink>
            </li>
            <li>
              <NavLink to="/shop">Shop</NavLink>
            </li>
            <li>
              <NavLink to="/location">Location</NavLink>
            </li>
          </ul>
        </div>
      </nav>

      <ul className="sidenav" id="mobile-demo">
        <li>
          <NavLink to="/home" onClick={() => instance.close()}>
            Home
          </NavLink>
        </li>
        <li>
          <NavLink to="/schedule" onClick={() => instance.close()}>
            Schedule
          </NavLink>
        </li>
        <li>
          <NavLink to="/classes" onClick={() => instance.close()}>
            Classes
          </NavLink>
        </li>
        <li>
          <NavLink to="/workshops" onClick={() => instance.close()}>
            Workshops
          </NavLink>
        </li>
        <li>
          <NavLink to="/profiles" onClick={() => instance.close()}>
            Profiles
          </NavLink>
        </li>
        <li>
          <NavLink to="/shop" onClick={() => instance.close()}>
            Shop
          </NavLink>
        </li>
        <li>
          <NavLink to="/location" onClick={() => instance.close()}>
            Location
          </NavLink>
        </li>
      </ul>
    </Fragment>
  );
};
