import React from "react";

export const CardPregnancy = () => {
  return (
    <div className="card" id="pregnancy">
      <div className="card-image">
        <img src="photo_pregnancy.jpg" />
      </div>
      <div className="card-content">
        <div className="card-title purple-text">Pregnancy Yoga</div>
        <p>
          Pregnancy is a time of change and celebration, as well as anticipation
          and anxiety. Yoga brings great awareness to what is going on within
          you and it gives you energy, restoring what you are putting into baby
          and also preparing you for birth.
        </p>
        <br />
        <p>
          Breath is the key. You will learn a technique that you can use anytime
          and will be especially helpful during labor. The breath is combined
          with postures (asanas) that safely strengthen your arms and legs and
          stretch the torso and spine to create space for growing baby. You may
          be familiar with Kegal exercises and in yoga terms this is called mula
          bandha (root lock). By practicing pelvic floor exercises you get a
          good start for both labor and after baby is born. Calmness comes from
          correct breathing and being in a friendly, supportive environment
          where you can focus on baby and you.
        </p>
        <br />
        <p>
          Class is offered in-person and via Zoom. Due to space limitations,
          please email or call (808) 944-8585 before coming to class. No
          drop-ins. We recommend starting after your thirteenth week. Ask your
          health care provider if yoga is appropriate for you. The teacher is
          Cathy Louise, a mom of three including twins.
        </p>
        <br />
        <p className="red-text">Get a free DVD with your first class</p>
      </div>
    </div>
  );
};
