import React, { useEffect } from "react";
import M from "materialize-css";

export const Profiles = () => {
  var handleScroll = (id) => {
    document.getElementById(id).scrollIntoView();
  };

  useEffect(() => {
    var elem = document.querySelector(".fixed-action-btn");
    document.addEventListener("DOMContentLoaded", function () {
      var instances = M.FloatingActionButton.init(elem, {});
    });

    window.addEventListener("scroll", (ev) => {
      if (!elem) return;
      if (window.scrollY >= 200) {
        elem.classList.remove("hide");
      } else {
        elem.classList.add("hide");
      }
    });
  });

  return (
    <div className="container">
      <div
        className="row buttons-option hide-on-large-only"
        style={{ paddingTop: "2em" }}
      >
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5 button-first-option"
            onClick={() => handleScroll("cathy")}
          >
            Cathy Louise
          </button>
        </div>
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5"
            onClick={() => handleScroll("glen")}
          >
            Glen Butcher
          </button>
        </div>
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5"
            onClick={() => handleScroll("yoko")}
          >
            Yoko Fujiwara
          </button>
        </div>
        <div className="col s12 m12 l2">
          <button
            className="waves-effect waves-light btn-flat purple lighten-5"
            onClick={() => handleScroll("gene")}
          >
            Gene Horita
          </button>
        </div>
      </div>

      <div className="row">
        <div className="col s12 l6 offset-l2">
          <ProfileCathy />
          <ProfileGlen />
          <ProfileYoko />
          <ProfileGene />
        </div>

        <div
          className="col l3 buttons-option hide-on-med-and-down"
          style={{ paddingTop: "1em" }}
        >
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5 button-first-option"
              onClick={() => handleScroll("cathy")}
            >
              Cathy Louise
            </button>
          </div>
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5"
              onClick={() => handleScroll("glen")}
            >
              Glen Butcher
            </button>
          </div>
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5"
              onClick={() => handleScroll("yoko")}
            >
              Yoko Fujiwara
            </button>
          </div>
          <div>
            <button
              className="waves-effect waves-light btn-flat purple lighten-5"
              onClick={() => handleScroll("gene")}
            >
              Gene Horita
            </button>
          </div>
        </div>
      </div>

      <div className="fixed-action-btn hide" style={{ marginBottom: "27px" }}>
        <button
          className="btn-floating btn-large blue"
          onClick={() => {
            window.scrollTo({
              top: 0,
              behavior: "smooth",
            });
          }}
        >
          <i className="material-icons">arrow_upward</i>
        </button>
      </div>
    </div>
  );
};

const ProfileCathy = () => {
  return (
    <div className="card" id="cathy">
      <div className="card-image">
        <img src="photo_cathy.jpg" />
      </div>
      <div className="card-content">
        <div className="card-title purple-text">Cathy Louise Broda</div>
        <p>
          Cathy Louise has been teaching Ashtanga Yoga for 31 years. Her first
          teacher was Beryl Bender Birch, who she did a two-year apprenticeship
          with in New York in the late 1980s. She also studied Ashtanga with
          David Life. Cathy Louise met Pattabhi Jois during his first visit to
          New York and received his blessing to teach Ashtanga in 2000.
        </p>
        <br />
        <p>
          Known for her transformative hands-on adjustments, Cathy Louise's
          teaching style owes much to certified Ashtanga teacher John Scott. She
          assisted John in his Mysore classes in London and then inherited her
          own Mysore style classes at Astanga Yoga London. She also taught
          vinyasa yoga at Triyoga and did a year-long apprenticeship in
          Pregnancy Yoga with Lynne Pinette. In London, Cathy Louise also met
          and studied with Nancy Gilgoff, who continues to be her mentor.
        </p>
        <br />
        <p>
          Cathy Louise's Pregnancy and Mom &amp; Baby classes draw on her
          experience as a mother of three. In her Ashtanga classes too, she
          openly shares her journey as a woman so students may learn how best to
          adjust the practice of yoga in all stages of their lives.
        </p>
        <br />
        <p>
          Watch and listen to interviews with Cathy Louise on the Kwok Talk show
          and the Ashtanga Dispatch podcast.
        </p>
      </div>
    </div>
  );
};

const ProfileGlen = () => {
  return (
    <div className="card" id="glen">
      <div className="card-image">
        <img src="photo_glen.jpg" />
      </div>
      <div className="card-content">
        <div className="card-title purple-text">Glen Butcher</div>
        <p>
          Glen has been practicing yoga for 33 years and teaching for 23 years.
          Originally from New Zealand, Glen's foundation in yoga was in the
          Iyengar style. After moving to London he did a two-year apprenticeship
          at a yoga school that specialized in individual instruction in small
          groups. He went on to study Ashtanga Yoga with certified teacher John
          Scott and assist his Mysore-style classes.
        </p>
        <br />
        <p>
          While in London, Glen also began what would become a 10-year learning
          experience with Shandor Remete, the founder of Shadow Yoga, studying
          with him in Europe, Asia, Australia, and the US. Glen taught Shadow
          Yoga in Honolulu for seven years before resuming his practice and
          teaching of Ashtanga Yoga. His personal studies include an interest in
          the classic hatha yoga texts.
        </p>
        <br />
        <p>
          Glen's teaching draws on his broad experience of hatha yoga, including
          its rehabilitative powers following three major knee surgeries he had
          as a result of a rugby injury at age 16. He guides each student
          through their own process of inquiry into the essence and practice of
          yoga, promoting a balance of ease and effort, and a celebration of
          what is possible along the way.
        </p>
      </div>
    </div>
  );
};

const ProfileYoko = () => {
  return (
    <div className="card" id="yoko">
      <div className="card-image">
        <img src="photo_yoko.jpg" />
      </div>
      <div className="card-content">
        <div className="card-title purple-text">Yoko Fujiwara</div>
        <p>
          Yoko started her Ashtanga Yoga practice in her hometown Tokyo in 2003.
          She has been teaching Ashtanga in Honolulu for the past 16 years. Her
          training includes a 16-month Mysore apprenticeship and a nine-month
          Pregnancy Yoga apprenticeship with Cathy Louise Broda. Yoko takes
          annual visits to Maui to study with Nancy Gilgoff.
        </p>
        <br />
        <p>
          On her first trip to Mysore, India in 2008, Yoko spent three months
          practicing with Sharath Jois and Saraswathi Rangaswamy. She studied
          Sanskrit and yoga sutra chanting in Mysore under Dr M A Jayashree.
          Yoko is also a licensed shiatsu therapist (MAT-11603). This training,
          along with her Ashtanga practice and teaching, allows her to see how
          the energetic body moves within the physical body.
        </p>
        <br />
        <p>
          Yoko has gone through many life changes since she started yoga, and
          she continues to develop an understanding of who she really is. She
          affirms that her yoga practice has evolved primarily due to the
          process of deep self-reflection and actively changing patterns rooted
          in fear, anger and insecurity.{" "}
          <a href="http://www.ashtangayoko.com/" target="_blank">
            www.ashtangayoko.com
          </a>
        </p>
      </div>
    </div>
  );
};

const ProfileGene = () => {
  return (
    <div className="card" id="gene">
      <div className="card-image">
        <img src="photo_gene2.jpg" />
      </div>
      <div className="card-content">
        <div className="card-title purple-text">Gene Horita</div>
        <p>
          Gene's journey into yoga began as a continuation of movement
          exploration after he graduated with a Master of Fine Arts in Dance,
          which was also around the same time he became a father. Ashtanga was
          his first introduction to yoga and the breadth and depth of the
          discipline soon revealed itself to be a lifelong practice that
          resonated deeply.
        </p>
        <br />
        <p>
          Gene completed a year-long Ashtanga Mysore apprenticeship with Cathy
          Louise Broda and a 200-hour Ashtanga Teacher Training with Yoko
          Fujiwara. In addition to teaching at Purple, Gene instructs four yoga
          courses under the Kinesiology &amp; Rehabilitation Science Department
          at the University of Hawaii at Manoa. He loves to travel and
          incorporate yoga training as part of his itinerary. He has completed
          training intensives with Manju Jois, Richard Freeman &amp; Mary
          Taylor, Mark &amp; Joanne Darby, Chuck Miller, and David Swenson.
        </p>
        <br />
        <p>
          Gene is certified as a Level 2 Warriors at Ease instructor. The WAE
          program specializes in training for holding classes in military and
          civilian settings for active duty personnel and veterans who may be
          experiencing or have experienced certain trauma-based encounters
          including PTSD, TBI, moral injury, and sexual trauma. Gene is also a
          lay practitioner and instructor of Korean Zen meditation. Currently,
          he directs a weekly sitting meditation and dharma discussion group.
        </p>
      </div>
    </div>
  );
};
