import React from "react";

export const CardVinyasaFlow = () => {
  return (
    <div className="card" id="vinyasa">
      <div className="card-image">
        <img src="photo_vin.jpg" />
      </div>
      <div className="card-content">
        <div className="card-title purple-text">Vinyasa Flow Yoga</div>
        <p>
          Vinyasa Flow Yoga is creative, fluid, challenging and fun. The teacher
          is free to improvise dynamic sequences drawn from the infinite
          possibilities of yoga asanas. In Saturday's Level Two class, Cathy
          Louise blends intermediate and some advanced poses at a vigorous pace.
        </p>
        <br />
        <p>
          Her flowing vinyasa style is based on popular classes she taught at
          London's Triyoga center, classes she playfully called swirly yoga. She
          typically begins her class with a variety of sun salutations,
          progresses through a range of asanas, including arm balances,
          backbends and inversions, and ends with a deep relaxation.
        </p>
        <br />
        <p>
          Vinyasa, literally translated as "placing (the limbs) in motion,"
          describes yoga practice that synchronizes breath and movement. It is
          the foundation for many of today's yoga styles. Vinyasa combined with
          ujjayi (a breathing technique) and bandhas (internal "locks") helps to
          focus the mind, build strength and stamina, and create heat to improve
          flexibility, reduce fat and eliminate toxins.
        </p>
      </div>
    </div>
  );
};
