import React from "react";

export const CardAshtangaLed = () => {
  return (
    <div className="card" id="ashtanga-led">
      <div className="card-content">
        <div className="card-title purple-text">Ashtanga Yoga Led class</div>
        <p>
          Ashtanga Yoga has two practice methods: the traditional Mysore style
          for individual instruction, and led classes where students enjoy the
          combined energy of a group who follow the teacher's count through all
          or part of the Primary Series. While students may choose to only
          attend led classes, we do encourage everyone to complement them with
          regular Mysore style practice.
        </p>
        <br />
        <p>
          Half Primary Series is sun salutations, standing poses and the sitting
          poses up to Navasana plus backbends and introduces the full closing
          sequence of inversions including shoulderstand and headstand. Full
          Primary Series goes beyond Navasana into more challenging asanas such
          as balancing poses plus backbends and the full closing sequence.
        </p>
      </div>
    </div>
  );
};
